<template>
  <div>
    <LoHeader class="lo_header" @query="queryData" :FilterItems="FilterItems" title="新加坡28开奖结果"></LoHeader>
    <mu-container ref="container" class="lo_content_box">
      <mu-load-more class="lo_content" :refreshing="refreshing" :loading="loading" style="width: 100%;"
                    @refresh="refresh" @load="load">
        <template v-for="item in tableData">
          <div>
            <div class="container">
              <div class="desc">
                <div class="title">
                  <div class="title_1">{{ item.LotteryStr }}</div>
                </div>
                <div class="priceDiv">
                  <div class="nums">
                    <template v-for="num in item.NumList">
                      <div :class="'num_'+num">{{num}}</div>
                    </template>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </template>
      </mu-load-more>
    </mu-container>
  </div>
</template>

<script>
import LoHeader from "../../components/LoHeader";
import {GetAwardListLastId} from '../../api/AwardList';
export default {
  name: "Zg28XjpAwardList",
  components: {
    LoHeader
  },
  data () {
    return {
      num: 10,
      refreshing: false,
      loading: false,

      FilterItems:[
        {label:'期号:',Value:'',type:"text",placeholder:"期号",icon:"",Name:"Period"},
      ],

      PageTotal:0,
      tableData: [],
      GroupData:{},
      query: {
        beginDay: '',
        endDay: '',
        Period:'',
        pageIndex: 1,
        pageSize: 10,
        GameType:this.Config.GameType.ZG28_41Jnd,
        FirstId:0,
      },
    }
  },
  methods: {
    refresh () {
      this.refreshing = true;
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.refreshing = false;
      });
    },
    load() {
      this.loading = true;
      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
        this.loading = false;
      });
    },
    getData(fun){
      GetAwardListLastId(this.query).then(response => {
        const { obj, code, msg } = response;
        if (code == 200) {
          if (this.query.FirstId==0) {
            this.tableData = obj.ListData;
          } else {
            this.tableData = this.tableData.concat(obj.ListData);
          }
          this.GroupData = obj.GroupData;
          this.PageTotal = obj.PageTotal;
          this.query.FirstId = obj.GroupData.FirstId;
          fun(true);
          resolve();
        } else {
          this.$toast.error(msg);
          fun(false);
        }
      }).catch(error => {
        fun(false);
      });
    },
    queryData(filterItems){
      this.query.pageIndex = 1;
      this.query.FirstId = 0;
      this.query.Period =0;
      this.query.beginDay = '';
      this.query.endDay = '';

      for (let key in filterItems) {
        if (filterItems[key].Name === 'Period') {
          this.query.Period = filterItems[key].Value;
        }else if (filterItems[key].Name === 'beginDay') {
          this.query.beginDay = filterItems[key].Value;
        } else if (filterItems[key].Name === 'endDay') {
          this.query.endDay = filterItems[key].Value;
        }
      }

      this.getData((v)=>{
        if (v==true){
          this.query.pageIndex = this.query.pageIndex+1
        }
      });
    },
  },
  created() {
    this.query.pageIndex = 1;
    this.query.FirstId = 0;
    this.getData((v)=>{
      if (v==true){
        this.query.pageIndex = this.query.pageIndex+1
      }
    });
  },
}
</script>

<style scoped>
@import url('../../assets/css/lottery_num.css');
@import url('../../assets/css/main.css');

.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #F2F2F2;
  justify-content: space-between;
  padding: 5px 11px;
  margin: 0 0 10px;
  background: #fff;
}
.desc {
  color: #8B8B8B;
  padding: 10px;
  text-align: left;
  font-size: 12px;
  width: 100%;
}
.title {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.title_l {
  font-weight: bold;
  color: #333;
  height: 20px;
  overflow: hidden;
  font-size: 14px;
}
.price {
  color: red;
  font-size: 16px;
  font-weight: 900;
}
.fsid {
  color: #8B8B8B;
}

.priceDiv {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
.xiaol {
  color: #8B8B8B;
  color: red;
}
.kc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.btn_01 {
  border: 1px solid #8B8B8B;
  border-radius: 3px;
  text-align: center;
  margin-left: 10px;
  padding: 3px 8px;
}
.btn {
  display: flex;
  justify-content: flex-end;
}
.main {
  margin-top: 8px;
  width: 100%;
  /*margin-top: 1.4rem;*/
  margin-bottom: 1.4rem;
}

</style>



